* {
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #23252C;
  font-family: Inter, sans-seriff;
}

.app-container {
  background-color: gray;
  display: flex;
  justify-content: center;
  width: 317px;
  margin-top: 30px;
  margin-bottom: 30px;
  border: 0px solid black;
  border-radius: 10px;
}

.info-container {
  width: 317px;
  margin-top: 40px;
  background-color: #1A1B21;
  display: grid;
  margin-top: 0;
  border: 0px solid black;
  border-radius: 10px 10px 0px 0px;
}

.info-img-container {
  width: 317px;
  height: 317px;
}

.info-proflie-pic {
  width: 317px;
  border: 0px solid black;
  border-radius: 10px 10px 0px 0px;
}

.info-name,
.info-sub-heading,
.info-sub-sub-heading {
  display: flex;
  justify-content: center;
  color: white;
}

.info-name {
  font-weight: 700;
  font-size: 25px;
  margin-bottom: 0px;
}

.info-sub-heading {
  font-weight: 400;
  font-size: 12.8px;
  color: #F3BF99;
  margin-top: 0px;
  margin-bottom: 0px;
  padding-top: 4px;
}

.info-sub-sub-heading {
  font-weight: 400;
  font-size: 10.24px;
  margin-top: 0px;
  margin-bottom: 0px;
  padding-top: 10px;
}

.info-button-container {
  display: flex;
  justify-content: space-evenly;
  padding: 20px 20px 20px 20px;
  align-items: center;
}

button {
  font-family: Inter, sans-seriff;
  width: 115px;
  height: 34px;
}

button > * {
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
}

button:hover {
  cursor: pointer;
  opacity: 0.75;
}

button:active {
  opacity: 0.5;
}

.info-white-button {
  color: #374151;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 5px 0 5px;
  border: 1px solid #D1D5DB;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
}

.info-email-icon {
  width: 16px;
}

.info-email-text {
  padding-left: 10px;
}

.info-blue-button {
  background: #5093E2;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 5px 0 5px;
  border: 0px solid #D1D5DB;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
}

.info-linkedIn-icon {
  width: 12px;
  padding-bottom: 2px;
}

.info-linkedIn-text {
  padding-left: 10px;
  font-weight: 500;
}

.about-container,
.interests-container {
  background-color: #1A1B21;
  display: flex;
  flex-direction: column;
  align-items: left;
  padding-left: 36px;
  padding-right: 34px;
  margin: 0;
  color: white;
}

.interests-container {
  padding-bottom: 20px;
}

.about-title,
.interests-title {
  margin: 0;
  padding-top: 12px;
  font-weight: 700;
  font-size: 16px;
}

.about-content,
.interests-content {
  margin: 0;
  padding-top: 5px;
  padding-bottom: 5px;
  font-weight: 400;
  font-size: 10.24px;
  line-height: 150%;
}

.footer-container {
  background-color: #161619;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 64px;
  padding-left: 73px;
  padding-right: 73px;
  border: 0px solid black;
  border-radius: 0px 0px 10px 10px;
}

.footer-container > img {
  width: 25px;
}